*,
*::after,
*::before {
	box-sizing: border-box;
}

:root {
	font-size: 16px;
	--color-text: #c27e73;
	--color-bg: #141418;
	--color-link: #a8dbe0;
	--color-link-hover: #fff;
	--color-title: #99dde2;
	--color-title-alt: #e2d2c3;
	--color-border: rgb(91 91 91 / 44%);
	--color-menu-item: #393634;
	--color-menu-item-alt: #e0d2c4;
	--color-menu-item-hover: #efafa5;
	--color-menu-item-desc: #f1f1f1;
	--color-desc-cont: #e2d2c3;
	--color-desc: #656569;
}

/* html, body {
	height: 100%;
	overflow: hidden;
} */

body {
	margin: 0;
	color: var(--color-text);
	background-color: var(--color-bg);
	/* font-family: "acumin-variable", sans-serif;*/
	/* font-family: 'AlphaLyrae', sans-serif; */

	/* font-variation-settings: "slnt" 0, "wdth" 98, "wght" 400; */
	text-transform: uppercase;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* https://vegaprotocol.github.io/alpha-lyrae/ */

/* @font-face {
	font-family: 'AlphaLyrae';
	src: url('../fonts/AlphaLyrae-Medium.woff2') format('woff2'),
		url('../fonts/AlphaLyrae-Medium.woff') format('woff');
	font-style: normal;
} */

@font-feature-values 'AlphaLyrae' {
	@styleset {
		ss01: 1;
		ss02: 2;
		ss03: 3;
		ss04: 4;
		ss05: 5
	}
}

.ImageTilesMenu__container {

	/* Page Loader */
	.js .loading::before,
	.js .loading::after {
		content: '';
		position: fixed;
		z-index: 1000;
	}

	.js .loading::before {
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: var(--color-bg);
	}

	.js .loading::after {
		top: 50%;
		left: 50%;
		width: 60px;
		height: 60px;
		margin: -30px 0 0 -30px;
		border-radius: 50%;
		opacity: 0.4;
		background: var(--color-link);
		animation: loaderAnim 0.7s linear infinite alternate forwards;

	}

	@keyframes loaderAnim {
		to {
			opacity: 1;
			transform: scale3d(0.5, 0.5, 1);
		}
	}

	a {
		text-decoration: none;
		color: var(--color-link);
		outline: none;
		cursor: pointer;
	}

	a:hover {
		color: var(--color-link-hover);
		outline: none;
	}

	/* Better focus styles from https://developer.mozilla.org/en-US/docs/Web/CSS/:focus-visible */
	a:focus {
		/* Provide a fallback style for browsers
	 that don't support :focus-visible */
		outline: none;
		background: lightgrey;
	}

	a:focus:not(:focus-visible) {
		/* Remove the focus indicator on mouse-focus for browsers
	 that do support :focus-visible */
		background: transparent;
	}

	a:focus-visible {
		/* Draw a very noticeable focus style for
	 keyboard-focus on browsers that do support
	 :focus-visible */
		outline: 2px solid red;
		background: transparent;
	}

	.unbutton {
		background: none;
		border: 0;
		padding: 0;
		margin: 0;
		font: inherit;
		cursor: pointer;
	}

	.unbutton:focus {
		outline: none;
	}

	main {
		/* overflow-y: auto; */
		position: relative;
		display: grid;
		grid-template-areas: 'main';
		width: 100%;
		height: auto;
		/* font-family: 'AlphaLyrae', sans-serif; */
	}

	.frame {
		grid-area: main;
		z-index: 1000;
		position: relative;
		width: 100%;
		padding: 1.5rem;
		display: grid;
		grid-template-columns: 100%;
		grid-template-areas: 'title' 'prev' 'sponsor';
		grid-gap: 0.5rem;
		justify-items: start;
		align-self: start;
		justify-self: start;
		pointer-events: none;
		align-items: center;
	}

	body #cdawrap {
		justify-self: start;
	}

	.frame a {
		pointer-events: auto;
	}

	.frame a:not(.frame__title-back) {
		white-space: nowrap;
		overflow: hidden;
		position: relative;
	}

	.frame a:not(.frame__title-back)::before {
		content: '';
		height: 1px;
		width: 100%;
		background: currentColor;
		position: absolute;
		top: 90%;
		transition: transform 0.3s;
		transform-origin: 0% 50%;
	}

	.frame a:not(.frame__title-back):hover::before {
		transform: scaleX(0);
		transform-origin: 100% 50%;
	}

	.frame__title {
		grid-area: title;
		display: flex;
	}

	.frame__title-main {
		font-size: inherit;
		margin: 0;
		font-weight: inherit;
	}

	.frame__title-back {
		position: relative;
		display: flex;
		align-items: flex-end;
	}

	.frame__title-back span {
		display: none;
	}

	.frame__title-back svg {
		fill: currentColor;
	}

	.frame__prev {
		grid-area: prev;
	}

	.title {
		text-align: center;
		font-family: Anton;
	}

	.menu {
		position: relative;
		grid-area: main;
		/* 	display: flex;  */
		flex-direction: column;
		justify-content: center;
	}

	.menu--hidden {
		pointer-events: none;
	}

	.menu__item:not(:last-child) {
		border-bottom: 1px solid var(--color-border);
	}

	.menu__item-title {
		white-space: nowrap;
		/* font-family: afronaut, serif; */
		/* font-family: 'AlphaLyrae', sans-serif; */

		font-weight: 400;
		text-transform: none;
		font-size: 5vh;
		grid-column: 2;
		color: var(--color-menu-item);
		transform: translateX(50px);
		display: block;
	}

	.menu__item:hover .menu__item-title,
	.menu__item:focus .menu__item-title {
		color: var(--color-menu-item-hover);
	}

	.menu__item .menu__item-title i {
		font-style: normal;
	}

	.menu__item:hover .menu__item-title i,
	.menu__item:focus .menu__item-title i {
		color: var(--color-menu-item-alt);
		transition: color 0.2s ease-out;
	}

	.menu__item-desc {
		opacity: 0;
		line-height: 1;
		max-width: 450px;
		font-weight: 500;
		display: none;
		font-size: clamp(0.5rem, 2.5vh, 1rem);
		color: var(--color-menu-item-desc);
	}

	.thumbgrid-wrap {
		position: relative;
		z-index: -1;
		grid-area: main;
		display: grid;
		pointer-events: none;
	}

	.thumbgrid {
		grid-area: 1 / -1;
		width: 100%;
		display: grid;

		/* 
			grid-template-columns: repeat(100, 1%);

			This property defines the columns of the grid.
			The repeat() function is a convenient way to define multiple columns or rows that have the same size. 
			Here, repeat(100, 1%) means that the grid will have 100 columns, each taking up 1% of the container's width. 
			This effectively divides the container into 100 equal columns.

			grid-template-rows: repeat(100, 1%);

			Similarly, this property defines the rows of the grid. Using repeat(100, 1%) 
			means that the grid will have 100 rows, each taking up 1% of the container's height. 
			This divides the container into 100 equal rows.
		*/
		grid-template-columns: repeat(100, 1%);
		grid-template-rows: repeat(100, 1%);
	}

	.thumbgrid-wrap .thumbgrid__item {
		opacity: 0;
		grid-area: var(--grid-row) / var(--grid-column);
		aspect-ratio: 4 / 3;
		width: 18vw;
	}

	.thumbgrid-wrap .thumbgrid--current .thumbgrid__item {
		opacity: 1;
	}

	.thumbgrid__item:nth-child(1) {
		--bg-pos-x: 0;
		--bg-pos-y: 0;
	}

	.thumbgrid__item:nth-child(2) {
		--bg-pos-x: 100%;
		--bg-pos-y: 0;
	}

	.thumbgrid__item:nth-child(3) {
		--bg-pos-x: 0;
		--bg-pos-y: 100%;
	}

	.thumbgrid__item:nth-child(4) {
		--bg-pos-x: 100%;
		--bg-pos-y: 100%;
	}

