	.thumbgrid__item-img {
		width: 100%;
		height: 100%;
		background-size: 200%;
		background-position: var(--bg-pos-x) var(--bg-pos-y);
	}

	.content-wrap {
		grid-area: main;
		display: none;
		pointer-events: none;
	}

	.content {
		pointer-events: none;
		opacity: 0;
		grid-area: 1 / -1;
		max-height: 100vh;
		padding: 8rem 1.5rem;
	}

	.content--current {
		pointer-events: auto;
		opacity: 1;
	}

	.content__title {
		font-family: afronaut, serif;
		font-weight: 400;
		text-transform: none;
		font-size: clamp(2rem, 10vw, 8rem);
		margin: 0;
		line-height: 1.2;
		color: var(--color-title);
		grid-column: 2;
	}

	.content__title i {
		font-style: normal;
		color: var(--color-title-alt);
	}

	.char-wrap {
		display: inline-block;
		position: relative;
		overflow: hidden;
	}

	.content__desc {
		grid-column: 2;
		line-height: 1;
		font-weight: 500;
		max-width: 550px;
		margin: 0;
		color: var(--color-desc);
	}

	.content__desc--cont {
		position: relative;
		color: var(--color-desc-cont);
	}

	.content__images {
		margin: 1.75rem 0 3rem 0;
	}

	.content__images .thumbgrid {
		height: auto;
		width: 100%;
		flex: none;
		aspect-ratio: 4 / 3;
		grid-template-columns: repeat(2, 50%);
		grid-template-rows: repeat(2, 50%);
		grid-area: thumbgrid;
	}

	.content__images .thumbgrid--content {
		grid-template-columns: 100%;
		grid-template-rows: 100%;
	}

	.thumbgrid--content .thumbgrid__item {
		grid-area: 1 / 1;
		aspect-ratio: 4 / 3;
	}

	.thumbgrid--content .thumbgrid__item-img {
		background-size: 100%;
		background-position: 0 0;
	}

	.thumb {
		height: 50vh;
		aspect-ratio: 4 / 3;
		width: auto;
		display: none;
	}

	.thumb--prev {
		grid-area: thumbprev;
		justify-self: end;
	}

	.thumb--next {
		grid-area: thumbnext;
	}

	.thumb__inner {
		width: 100%;
		height: 100%;
		background-size: cover;
		background-position: 50% 50%;
		filter: brightness(0.3);
	}

	.content__back {
		justify-self: start;
		grid-column: 2;
		color: #fff;
		text-transform: uppercase;
		padding: 0.15rem 0.85rem 0.25rem;
		border: 1px solid rgba(255, 255, 255, 0.3);
		border-radius: 2rem;
	}

	.page_title {
		margin-bottom: 8rem;
		text-decoration: underline;
		font-size: 8vh;
		text-align: center;
	}

	@media screen and (min-width: 53em) {
		.frame {
			position: fixed;
			height: 100vh;
			grid-template-columns: auto 1fr;
			grid-template-rows: auto auto;
			grid-template-areas: 'title sponsor' 'prev ...';
			align-content: space-between;
			justify-items: start;
			grid-gap: 2rem;
			padding: 0.75rem;
		}

		.menu__item {
			flex: 1;
			align-items: center;
			gap: 1.5rem;
			display: grid;
			grid-template-columns: 20vw auto auto 1fr;
		}

		.menu__item-title {
			font-size: clamp(1rem, 9vh, 5rem);
		}

		.menu__item-desc {
			display: block;
		}

		.content {
			padding: 1.5rem 0 1.5rem 0;
			align-content: end;
			/* display: grid; */
			column-gap: 2vw;
			grid-template-columns: 20vw 1fr;
			grid-template-rows: auto 12vw auto auto;
		}

		.content__images {
			display: grid;
			gap: 2vw;
			grid-template-columns: 20vw 1fr auto;
			grid-template-areas: 'thumbprev thumbgrid thumbnext';
		}

		.thumb {
			display: block;
		}

		.content__images .thumbgrid {
			height: 50vh;
			width: auto;
		}

		body #cdawrap {
			justify-self: end;
		}
	}

}